import "./App.css";
import { useEffect, useState } from "react";
import { Tabs, TabList, Tab, TabPanel } from "react-aria-components";

function safeDecode(value) {
  try {
    return decodeURIComponent(value);
  } catch {
    return value;
  }
}

function parseReviews(reviewsParam) {
  try {
    const decoded = safeDecode(reviewsParam);
    console.log("Decoded reviews:", decoded);
    if (decoded.trim().startsWith("[") && decoded.trim().endsWith("]")) {
      return JSON.parse(decoded);
    } else {
      console.error("Invalid JSON format:", decoded);
      return [];
    }
  } catch (error) {
    console.error("Error parsing reviews:", error);
    return [];
  }
}

function Review({ isPositive, full_name, text }) {
  const reviewClass = isPositive ? "review-positive" : "review-negative";
  const label = isPositive ? "" : "";
  const avatarLetter = full_name?.[0]?.toUpperCase() || "?";

  return (
    <div className={`review ${reviewClass}`}>
      <div className="review-avatar">{avatarLetter}</div>
      <div className="review-content">
        <div className="review-header">
          <div className="review-author">{full_name}</div>
          <div className="review-status">{label}</div>
        </div>
        <div className="review-text">{text}</div>
      </div>
    </div>
  );
}

function App() {
  const [username, setUsername] = useState("");
  const [registration, setRegistration] = useState("");
  const [reviews, setReviews] = useState([]);

  useEffect(() => {
    const params = new URLSearchParams(window.location.search);

    setUsername(safeDecode(params.get("full_name") || ""));
    setRegistration(safeDecode(params.get("registration") || ""));

    const reviewsParam = params.get("reviews");
    if (reviewsParam) {
      setReviews(parseReviews(reviewsParam));
    }
  }, []);

  const positiveReviews = reviews.filter((review) => review.status === true);
  const negativeReviews = reviews.filter((review) => review.status === false);

  return (
    <div className="container">
      {username && <h2 className="username">{username}</h2>}
      {registration && <p className="registration">{registration}</p>}

      <Tabs>
        <TabList>
          <Tab id="All" data-key="All">
            <b>  {reviews.length}</b>
          </Tab>
          <Tab id="positive" data-key="positive">
            <b> {positiveReviews.length}</b>
          </Tab>
          <Tab id="negative" data-key="negative">
            <b> {negativeReviews.length}</b>
          </Tab>
        </TabList>
        <div className="tab-divider" />

        <TabPanel id="All">
          {reviews.length === 0
            ? " ."
            : reviews.map((review, index) => (
                <Review
                  key={index}
                  isPositive={review.status}
                  full_name={review.full_name}
                  text={review.text}
                />
              ))}
        </TabPanel>

        <TabPanel id="positive">
          {positiveReviews.length === 0
            ? "  ."
            : positiveReviews.map((review, index) => (
                <Review
                  key={index}
                  isPositive={review.status}
                  full_name={review.full_name}
                  text={review.text}
                />
              ))}
        </TabPanel>

        <TabPanel id="negative">
          {negativeReviews.length === 0
            ? "  ."
            : negativeReviews.map((review, index) => (
                <Review
                  key={index}
                  isPositive={review.status}
                  full_name={review.full_name}
                  text={review.text}
                />
              ))}
        </TabPanel>
      </Tabs>
    </div>
  );
}

export default App;